.center ,.left, .right {
  display:-webkit-flex;
  display:flex;
  -webkit-flex-direction: row;
          flex-direction: row;
  -webkit-justify-content: center;
          justify-content: center;
  -webkit-align-items: center;
          align-items: center;
}

.left {
  -webkit-justify-content: flex-start;
          justify-content: flex-start;
}
.right {
  -webkit-justify-content: flex-end;
          justify-content: flex-end;
}


ul {
    margin: 0;
    padding: 0;
    list-style: none;
}

.company_list_item {
    margin-top: 6px;
}
.company_list_item .title {
    color:#00b1b2;
}
.company_list_item span {
    margin-right: 1rem;
}
.vote_button_container {
    width:50%;
}
.vote_button_container  .button {
    width:6rem;
}
.vote_footer {
    -webkit-justify-content: flex-start;
            justify-content: flex-start;
}

.survey-card-container {
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: row;
            flex-direction: row;
    -webkit-justify-content: space-between;
            justify-content: space-between;
}
.company-info {
    -webkit-order:99;
            order:99;
    width:10rem;
    min-width:10rem;

}

#survey-list-container {
    width:100%;
}

#survey-list li {
    margin-top:1rem;
}
#survey-list li:first-child {
    margin-top:inherit;
}
.survey-card-container .detail{
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: column;
            flex-direction: column;
}
.survey-card-container .detail .questions span{
    margin-left:2rem;
}
.survey-card-container .detail .questions span:first-child{
    margin-left:0;
}

.survey-card-container .detail .extra{
    max-width:80%;

}
.survey-card-container .detail .clickIcon{
    cursor:pointer;
}

